import React from 'react'
import moment from 'moment'

import './App.scss'

class App extends React.Component {
  constructor() {
    super()

    this.state = {
      loveCount: 0,
    }

    this.countStart = moment.unix(1517356800)
  }

  componentWillMount() {
    const now = moment()

    this.setState({
      loveCount: moment(now - this.countStart)
    })
  }

  componentDidMount() {
    setInterval(() => {
      const now = new Date().getTime()

      this.setState({
        loveCount: moment(moment.unix(now / 1000) - this.countStart)
      })
    }, 1000)
  }

  render() {
    const { loveCount } = this.state

    return (
      <div className='container'>
        <div className='polaroid_wrapper'>
          <img src={ require('assets/youandi.jpg') } />
          <p>You and I..</p>
          <p>{ loveCount.format('D [days] H [hours] s [seconds]') }</p>
        </div>
      </div>
    )
  }
}

export default App



